  cursor: pointer;
  width: 50px;
  height: 50px; }

/*×に変化*/
.openbtn1 span {
  display: inline-block;
  transition: all .4s;
  position: absolute;
  left: 14px;
  height: 3px;
  border-radius: 2px;
  background-color: #666;
  width: 45%; }

.openbtn1 span:nth-of-type(1) {
  top: 15px; }

.openbtn1 span:nth-of-type(2) {
  top: 23px; }

.openbtn1 span:nth-of-type(3) {
  top: 31px; }

.openbtn1.active span:nth-of-type(1) {
  top: 18px;
  left: 18px;
